







import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'
@Component
export default class Types extends Vue {
  @Prop(String) value!: string
  selectType(type: string): void {
    if (type !== '-' && type !== '+') {
      throw new Error('type is unknow')
    }
    this.$emit('update:value', type)
  }
}
